    margin-bottom: 15px;
  }
}
@media (max-width: 600px) {
  .organization-section__title {
    margin-left: auto;
    line-height: 1.2;
    margin-bottom: 30px;
    padding-left: 60px;
  }
  .organization-section__title .padding-left {
    padding-left: 0;
    margin-left: -60px;
  }
}
.organization-section__text {
  line-height: 1.5;
  margin-bottom: 60px;
}
@media (max-width: 1440px) {
  .organization-section__text {
    margin-bottom: 45px;
    line-height: 1.7;
  }
}
@media (max-width: 1280px) {
  .organization-section__text {
    margin-bottom: 32px;
  }
}
@media (max-width: 600px) {
  .organization-section__text {
    line-height: 1.6;
    margin-bottom: 40px;
  }
}
.organization-section__btn {
  margin: 0 auto;
}

.advantages-section {
  background: url(../img/bg-main.jpg);
}
.advantages-section__items {
  display: grid;
  gap: 2px;
  justify-content: center;
  grid-auto-rows: 1fr;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
}
@media screen and (min-width: 2000px) {
  .advantages-section__items {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (max-width: 1700px) {
  .advantages-section__items {
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  }
}
@media (max-width: 1440px) {
  .advantages-section__items {
    grid-template-columns: repeat(auto-fill, minmax(310px, 1fr));
  }
}
@media (max-width: 1280px) {
  .advantages-section__items {
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  }
}
@media (max-width: 768px) {
  .advantages-section__items {
    grid-template-columns: repeat(auto-fill, minmax(185px, 1fr));
  }
}
.advantages-section__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 450px;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 39px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #e3ded8;
  background: #808080;
  padding: 10px;
}
@media (max-width: 1440px) {
  .advantages-section__link {
    font-size: 29px;
    min-height: 335px;
  }
}
@media (max-width: 1280px) {
  .advantages-section__link {
    font-size: 26px;
    min-height: 300px;
  }
}
@media (max-width: 768px) {
  .advantages-section__link {
    font-size: 15px;
    min-height: 182px;
  }
}

.questions-section {
  background: url(../img/grey-bg.jpg);
}
.questions-section__title {
  letter-spacing: -0.025em;
  line-height: 1;
  margin-bottom: 45px;
}
@media (max-width: 600px) {
  .questions-section__title {
    margin-bottom: 20px;
  }
}
.questions-section__items {
  margin-bottom: 20px;
}
@media (max-width: 1440px) {
  .questions-section__items {
    margin-bottom: 40px;
  }
}
@media (max-width: 600px) {
  .questions-section__items {
    margin-bottom: 5px;
  }
}
.questions-section__item:not(:last-child) span {
  border-bottom: 2px solid #232323;
}
@media (max-width: 600px) {
  .questions-section__item:not(:last-child) span {
    border-bottom: none;
  }
}
.questions-section__item span {
  display: block;
  padding: 87px 0;
}
@media (max-width: 1440px) {
  .questions-section__item span {
    padding: 63px 0;
  }
}
@media (max-width: 1280px) {
  .questions-section__item span {
    padding: 58px 0;
  }
}
@media (max-width: 768px) {
  .questions-section__item span {
    padding: 22px 0;
  }
}
@media (max-width: 600px) {
  .questions-section__item span {
    padding: 22px 0;
    font-size: 16px;
  }
}
.questions-section__btn {
  margin: 0 auto;
}